<template lang="html">
  <div class="complaint-notes-page">
    <!-- 头部 -->
    <default-head header="我的投诉记录"></default-head>
    <div
     v-infinite-scroll="loadMore"
    :infinite-scroll-disabled="loading"
    :infinite-scroll-distance="10"
    :infinite-scroll-immediate-check="true"
    >
      <complaint-notes-list v-for="(item, index) in complainList" :item="item" :key="index" :index="index"
        @click-first="cancelComplain" @click-second="sureComplain"
      ></complaint-notes-list>
    </div>
    <div v-if="complainList.length <= 0" class="bycomplain-no-data">
      <no-data></no-data>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import _ from 'underscore'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import complaintNotesList from '@/components/complaintNotes/complaintNotesList'
import noData from '@/components/common/noData'

export default {
  data () {
    return {
      complainList: [],
      loading: false,
      page: 1,
      total: 1
    }
  },
  components: { defaultHead, complaintNotesList, noData },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      this.ajaxGetComplainList({userId: this.userId})
    },
    cancelComplain (item) {
      let data = {userId: this.userId, id: item.id, status: 5}
      this.complainMethod('您确定要取消该条投诉 ？', data)
    },
    sureComplain (item) {
      let data = {userId: this.userId, id: item.id, status: 4}
      this.complainMethod('您确定要投诉该用户 ？', data)
    },
    complainMethod (message, data) {
      let self = this
      minutils.MessageBoxConfirm({
        message: message,
        title: '提示',
        success (action) {
          self.ajaxChangeStatus(data)
        }
      })
    },
    loadMore: _.throttle(function () {
      // 关闭无线滚动
      if (this.page >= this.total) {
        this.loading = true
        this.page++
        this.ajaxGetComplainList({userId: this.userId, pageIndex: this.page, pageSize: 10, callback: this.changeLoading})
      }
    }, 300),
    // 启动无线滚动
    changeLoading () {
      this.loading = false
    },
    ajaxGetComplainList ({userId = 0, pageIndex = 1, pageSize = 10, callback = null} = {}) {
      let data = {userId, pageIndex, pageSize}
      minutils.IndicatorOpen('加载中...')
      api.getComplainList(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        typeof callback === 'function' && callback()
        if (result.isSuccess && result.data) {
          if (pageIndex === 1) {
            this.complainList = result.data.list
          } else {
            this.complainList = [...this.complainList, ...result.data.list]
          }
          this.total = result.data.total
          this.page = result.data.pageIndex
        }
      })
    },
    ajaxChangeStatus (data) {
      api.changeStatus(data).then(response => {
        let result = response.body
        let self = this
        if (result.isSuccess && result.data) {
          minutils.MessageBox({
            message: '操作成功',
            callback () { self.initPage() }
          })
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.complaint-notes-page {
  padding-top: .88rem;

}

</style>



