<template lang="html">
    <div class="complaint-notes-list">
      <div class="complaint-notes-list__info clearfix">
        <span class="fl person">{{item.userType === 0 ? '个人' : '企业'}}{{item.realName}} ({{item.mobile}})投诉</span>
        <span class="fr time">{{item.updateTime}}</span>
      </div>
      <div class="complaint-notes-list__status clearfix">
        <span class="fl msg">{{item.content}}</span>
        <span class="fr status" v-if="isAppeal">{{this.status[item.status]}}</span>
      </div>
      <div class="complaint-notes-list__button clearfix">
        <span  v-show="leftButton === '投诉申诉'" class="cancel fl" v-if="!isAppeal || (item.status && item.status === 1)" @click="$emit('click-first', item)" >{{leftButton}}</span>
        <span  v-show="leftButton === '取消投诉'" class="cancel fl" v-if="!isAppeal || (item.status && (item.status === 2 || item.status === 3))" @click="$emit('click-first', item)" >{{leftButton}}</span>
        <span  class="confirm fl" v-if="isAppeal && item.status && item.status === 2"  @click="$emit('click-second', item)" >确认投诉</span>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    isAppeal: {
      type: Boolean,
      default: true
    },
    leftButton: {
      type: String,
      default: '取消投诉'
    },
    item: Object,
    index: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      status: ['审核中', '投诉失败', '处理中', '系统确认中', '投诉成功', '已洽谈']
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.complaint-notes-list {
  padding: 0 .22rem;
  font-size: .3rem;
  background: @whiteColor;
  border-bottom: 1px solid @lineColor;
  .complaint-notes-list__info {
    height: .6rem;
    line-height: .3rem;
    padding: .2rem 0 .1rem;
    .person {
      font-size: .3rem;
    }
    .time {
      font-size: .24rem;
      color: @lightColor;
    }
  }
  .complaint-notes-list__status {
    height: .7rem;
    line-height: .3rem;
    padding: .1rem 0 .3rem;
    .msg {
      color: @midleColor;
    }
    .status {
      color: @redColor;
    }
  }
  .complaint-notes-list__button {
    padding-bottom: .3rem;
    span {
      width: 1.6rem;
      height: .68rem;
      line-height: .68rem;
      text-align: center;
      border-radius: .05rem;
      background: @activeColor;
      color: @whiteColor;
      &:nth-child(2) {
        margin-left: .4rem;
      }
    }
  }
}
</style>



